body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

h1 {
  text-align: center;
}

form {
  display: flex;
  flex-direction: column;
}

#name,
#file {
  width: 20%;
  height: 1.6rem;
  margin: 10px;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.05rem;
  border: 1px solid black;
  border-radius: 5px;
}

#file {
  cursor: pointer;
}

#submit {
  width: 8%;
  margin: 10px;
  margin-left: auto;
  margin-right: auto;
  height: 2rem;
  font-size: 1.1rem;
}

table {
  width: 100%;
  margin-bottom: 1rem;
  background-color: transparent
}

h2 {
  margin-bottom: 30px;
}

table td {
  padding: .75rem;
  vertical-align: top;
  border-top: 1px solid #dee2e6
}

table thead th {
  vertical-align: bottom;
  border-bottom: 2px solid #dee2e6
}

th,
td {
  width: 20rem;
  text-align: center;
}
table{
  border-collapse: collapse;
}